import React, { useState, useEffect } from "react";
import A from "../A";
import Button from "../Button";
import "./style.css";
import Spinner from "../Spinner";
import { getTradePairs } from "../../http/wallet-calls";
import { GetAssetImage } from "../../globalFunctions";
import walletCheck from "../../hooks/walletCheck";

const RewardsWidget = (props) => {
  const [loading, setLoading] = useState(false);
  const [status, setStatus] = useState(null);

  useEffect(() => {
    walletCheck().then((resp) => {
      try {
        setStatus(resp.data.status);
      } catch (error) {}
    });
  }, []);

  return (
    <div
      className={
        " empty-inner-container-with-border widget rewardsWidget " +
        (props.variant != null && " " + props.variant)
      }
    >
      <div className="col">
        <ul className="list-group w-100">
          <li className="widgetTitle">
            <img src={GetAssetImage("icon-material-royalty.svg")} />
            <span>Rewards</span>
          </li>
        </ul>
        {/* <div className="list-group-item d-flex justify-content-between align-items-center header-drak widgetTitle">
                  <div>BUY CRYPTOCURRENCY</div>
                </div> */}
        <div className="widgetBody">
          {loading ? (
            <Spinner />
          ) : (
            <div className="flex-row">
              <img
                src={GetAssetImage("icon-material-stars.svg")}
                className="icon-star"
              />

              {status == "Accept" ? (
                <div>
                  <p>
                    Congrats! You have earned <strong>400 Points</strong> on
                    your KYC.
                  </p>
                  <p>
                    Refer your friend and earn <strong>500 Points</strong>.
                  </p>
                  <A href={"/wallet/verification"}>
                    <Button variant="primary-outline" size="big">
                      Earn Points
                    </Button>
                  </A>
                </div>
              ) : (
                <div>
                  <p>
                    Congrats! You have earned <strong>100 Points</strong> on
                    your Registration.
                  </p>
                  <p>
                    Complete your KYC and earn <strong>400 Points</strong>.
                  </p>
                  <A href={"/wallet/verification"}>
                    <Button variant="primary-outline" size="big">
                      Earn Points
                    </Button>
                  </A>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default RewardsWidget;
