import React from 'react';
import { alertBox } from '../../../commonRedux/';
import A from '../../../components/A';
import Button from '../../../components/Button';
import GigFilter from '../../../components/Filter/gigFilter';
import CreateGigWidget from '../../../components/Gigs/Create';
import Gig from '../../../components/Gigs/Gig';
import GigRequest from '../../../components/Gigs/GigRequest';
import PopularGigs from '../../../components/Gigs/PopularGigs';
import Modal from '../../../components/Popup';
import RewardsWidget from '../../../components/RewardsWidget';
import TabsUI from '../../../components/Tabs/index';
import { allGigRequests, getGigs } from '../../../http/gig-calls';
import { postReport, postUnReport } from '../../../http/http-calls';
import './style.css';

class PopularGigsWidget extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            limit: 4,
            page: 1,
            cid: 0,
            posts: [],
            category: '',
            reason: '',
            reportModal:false,
            data: {
                category: "",
                subCategory : "",
                priceFrom : "",
                priceTo : "",
                rating : "",
                key: ""
            },
            lastData: null,
            lastI: null,
            lastType:null,
            gigs: [],
            currentTab:0
        }
    }

    reportModal = (p = null, i = null, t=null) => {
        this.setState({
            lastData: p,
            lastI: i,
            lastType: t
        });
        this.setState({reportModal: !this.state.reportModal}) // true/false toggle
    };

    report = () => {
            var lastData = this.state.lastData;
            var lastI = this.state.lastI;
            var link = "gigonomy/gig/"+lastData._id;
            postReport({ id: lastData._id, type: this.state.lastType, link, category: this.state.category, reason: this.state.reason })
            .then(async resp => {
                alertBox(true, resp.message, 'success');
                var temp = this.state.posts;
                temp[lastI].reported = 1;
                this.setState({ reportModal: false, posts: temp });
            }, error => {
                alertBox(true, error.data.message);
                this.setState({ reportModal: false});
            });
    
    };
    unReport = (post, i) => {
        postUnReport({ id: post._id })
        .then(async resp => {
            alertBox(true, resp.message, 'success');
            var temp = this.state.posts;
            temp[i].reported = 0;
            this.setState({ reportModal: false, posts: temp });
        }, error => {
            alertBox(true, error.data.message);
        });
    };

    componentDidMount = () => {
        // this.getData();
        // this.getRequests();
        this.getData();
    }

    categoryChange = (c) => {
        this.setState({
            cid: c,
            currentTab: 0
        })
    }
    
    dataChange = (data = {}) => {  
        this.getData(data);
    }

    getData =(data = {}) => {
        this.setState({
            gigs: []
        })
        var d = data;
        console.log(d)
        d['page']  = this.state.page;
        d['limit'] = this.state.limit;
        d['userid'] = this.props.currentUser._id;
        getGigs(d).then(resp => {
            this.setState({
                gigs: resp.post
            }, () => {
                console.log(this.state.gigs)
            });
        }, err=>{
            console.log(err);
        })
    }

    changeTab = (newValue) => {
        this.setState({currentTab: newValue});
    };

    render() {
        return (
            <div className="gigsWidget">
                    
                    <div className="d-flex align-items-center justify-content-between">
                        <div>
                            <h3 className="gtitle">Talents you may like</h3>
                            <h4 className="gsubtitle">Gigs you might be interested in</h4>
                        </div>
                        <A href={"/passionomy/gigs"}><Button variant="primary-outline">View All</Button> </A>
                    </div>
                    <div className="clearfix">
                
                    {
                        this.state.gigs.length == 0 ?
                        <p className="no-found">No Gigs Found</p>
                        :
                        <div className="row-1">
                        {
                        this.state.gigs.map((gig, i) => {
                            // return <TopThumbCard  report={() => this.reportModal(gig, i, 'gig')} unReport={()=>this.unReport(gig, i)} url={"/passionomy/gig/"+gig._id} currentUser={this.props.currentUser} post={gig} removePost={this.removePost}  base={"/passionomy/edit/"} />
                            return <Gig {...this.props} post={gig} />
                        })
                        }
                        </div>
                    }
                </div>

                
                    
                </div>
        );
    }
}

export default PopularGigsWidget;